import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Paper, Typography, Avatar, IconButton, Card, CardContent } from '@material-ui/core';
import { MailOutline, Phone, LinkedIn, GitHub } from '@material-ui/icons';
import avatar from '../Images/avatar.jpg';
import { aquamarine } from 'color-name';
import pdf from '../Documents/AndieMontoya-May2020-Resume.pdf';
import { linkSync } from 'fs';

const useStyles = makeStyles(theme => ({
     title: { 
        fontFamily: 'Lobster, cursive',
        paddingTop: '50px', 
        marginBottom: '8px', 
        color: '#63c7e6'
     },
     main: { 
         position: 'relative',
         minheight: '100vh',
         width: '100vw',
         margin: 0
     }, 
     container: { 
        height: '100%',
        display: 'flex',
        alignItems: 'center', 
        justifyContent: 'center',
     },
     projectBody: { 
        fontFamily: 'Open Sans, sans-serif', 
        fontSize: '1.25rem' 
     }, 
     projectCard: { 
         margin: '16px',
         padding: '16px 8px',
         textAlign: 'left',
        width: '450px', 
        minHeight: '480px',
        backgroundColor: '#fafafa'
     },
     projects: { 
         marginTop: '4rem',
         maxWidth: '90%',
         marginBottom: '4rem',
         display: 'flex', 
         justifyContent: 'center', 
         flexWrap: 'wrap'
     }, 
     projectTitle: { 
         fontFamily: 'Lobster, cursive', 
     }, 
     projectSubtitle: { 
        fontFamily: 'Open Sans, sans-serif', 
        color: '#63c7e6'
     }, 
     skillsArea: { 
         display: 'flex', 
         flexWrap: 'wrap', 
     }, 
     skill: { 
         padding: '8px', 
         margin: '4px 2px',
         borderRadius: '4px', 
         fontSize: '1.25rem',
         color: 'white',
         fontFamily: 'Open Sans, sans-serif', 
         backgroundColor: '#63c7e6'
     }
  }));

const Projects = () => {  
    const classes = useStyles();

    const projects = [
        { 
            title: 'UBaeC',
            subtitle: 'A native Android App', 
            description: 'UBaeC is a dating app for UBC students. Built in a group of 4. Worked on implementing the UI, client-server communication via REST, and push notifications using Firebase cloud messaging.',
            skills: ['Java', 'Android', 'Firebase']
        }, 
        { 
            title: 'Trial Matcher',
            subtitle: 'A web and  mobile app', 
            description: 'A project for Vancouver General Hospital, Trial Matcher is a platform for researchers to find patients for their clinical trials. Researchers can enter trial criteria through a web app while patients can be screened for eligiblity through surveys on the mobile app. Built in a group of 5. Worked on implementing the web UI and client-server communication via REST.',
            skills: ['React', 'JavaScript', 'Jest', 'Enzyme']
        },
        { 
            title: 'Mckesson Bot',
            subtitle: 'A Slackbot', 
            description: 'Slackbot to print a cafeteria\'s daily menu. Retrieves pdf and parses into a formatted menu for the day or week.',
            skills: ['Node', 'JavaScript', 'Express', 'Heroku', 'Slack']
        }, 
        { 
            title: 'DeWatch Server',
            subtitle: 'A web server', 
            description: 'An HTTP server that implements a simple CRUD API for a running app. Follows REST principles and incudes authentication through JWT tokens.',
            skills: ['Node', 'JavaScript', 'Express', 'MongoDB', 'Heroku']
        }, 
        { 
            title: 'SuperRent',
            subtitle: 'A database management service', 
            description: 'A database management service for a fictional car rental company. Supports typical transactions like returns, rentals, and reservations. Built in a group of 3.',
            skills: ['JDBC', 'Oracle', 'Java']
        }, 
        { 
            title: 'Smart Rise',
            subtitle: 'A smart home system', 
            description: 'De1-SoC takes user input via tablet to control lights and music, retrieve weather data, and schedule events and alarms. Includes a companion Android app for similar functionalities, voice command options, and retrieving additional data. Built in group of 5. Worked mainly on Android app and setting up the Raspberry Pi as a music player.',
            skills: ['De1-Soc', 'Verilog', 'Raspberry Pi', 'Android', 'Java', 'Python', 'Yelp API']
        }, 
    ]
    return (
            <div id="projects-section" className={classes.main}>
                <Typography className={classes.title} variant="h2">Projects</Typography> 
                <div className={classes.container}> 
                    <div className={classes.projects}> 
                        {projects.map(project => <ProjectCard  {...project}/>)}
                    </div>
                </div>
            </div> 
    ); 
}

export default Projects;

const ProjectCard = props => {  
    const classes = useStyles();
    return (
        <Card elevation={5} className={classes.projectCard}> 
        <CardContent>
            <Typography className={classes.projectTitle} variant="h3">{props.title}</Typography> 
            <Typography className={classes.projectSubtitle} variant="h6">{props.subtitle}</Typography>
            <Typography className={classes.projectBody} variant="body2">{props.description}</Typography>
            <SkillsList skills={props.skills} />
        </CardContent>
        </Card>
    ); 
}

const SkillsList = props => { 
    const classes = useStyles(); 
    return ( 
        <div className={classes.skillsArea}> 
            {props.skills.map(skill => <div className={classes.skill}>{skill}</div>)}
        </div>
    );
}
