import React, { useState } from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail'; 
import MenuIcon from '@material-ui/icons/Menu';
import { IconButton } from '@material-ui/core';
import LocalFloristRoundedIcon from '@material-ui/icons/LocalFloristRounded';
import AssignmentRoundedIcon from '@material-ui/icons/AssignmentRounded';
import BuildRoundedIcon from '@material-ui/icons/BuildRounded';
import EmailRoundedIcon from '@material-ui/icons/EmailRounded';
import { EmailRounded } from '@material-ui/icons';
import { Link, scroller as scroll } from "react-scroll";
import pdf from '../Documents/AndieMontoya-May2020-Resume.pdf';

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  listItem: {
    fontFamily: 'Lobster, cursive',
    fontSize: '24px'
  },
  fab: {
    margin: 0,
    top: 20,
    left: 20,
    bottom: 'auto',
    right: 'auto',
    position: 'fixed',
    zIndex: 5

  },
  icon: {
    width: '40px', 
    height: '40px'
  }, 
  sideIcon: { 
      color: '#63c7e6'
  }
});

export default function MenuDrawer() {
  const classes = useStyles();
  const [menu, setMenu] = useState(false);

  const toggleDrawer = (open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setMenu(open);
  };

  const clickHandler = (id) => () => { 
    scroll.scrollTo(id, {
        duration: 1500,
        delay: 100,
        smooth: true,
    })
  }

  const openPdf = () => window.open(pdf);

  const icons = { 
      'About': <LocalFloristRoundedIcon/>, 
      'Resume': <AssignmentRoundedIcon/>,
      'Projects': <BuildRoundedIcon/>,
      'Contact': <EmailRounded/>
  }

  const list =
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      <List>
        {['About', 'Resume', 'Projects', 'Contact'].map((text) => (
          <ListItem className={classes.listItem} button key={text} onClick={text === 'Resume' ? openPdf: clickHandler(`${text.toLowerCase()}-section`)}>
            <ListItemIcon className={classes.sideIcon}>{icons[text]}</ListItemIcon>
            <ListItemText disableTypography primary={text} />
          </ListItem>
        ))}
      </List>
    </div>;

  return (
    <div className={classes.fab}>
        <React.Fragment>
          <IconButton  onClick={toggleDrawer(true)}> 
            <MenuIcon className={classes.icon}/>
          </IconButton>
          <Drawer anchor="left" open={menu} onClose={toggleDrawer(false)}>
            {list}
          </Drawer>
        </React.Fragment>
    </div>
  );
}
