import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Paper, Typography, Avatar, IconButton } from '@material-ui/core';
import { MailOutline, Phone, LinkedIn, GitHub } from '@material-ui/icons';
import avatar from '../Images/avatar.jpg';
import { aquamarine } from 'color-name';
import pdf from '../Documents/AndieMontoya-May2020-Resume.pdf';

const useStyles = makeStyles(theme => ({
    wrapIcon: {
      verticalAlign: 'middle',
      display: 'inline-flex', 
      marginTop: '8px', 
      color: '#585959', 
      fontFamily: 'Open Sans, sans-serif'
     }, 
     avatar: { 
         margin: 'auto', 
         width: '200px', 
         height: '200px',
         marginBottom: '40px', 
     }, 
     sideIcon: { 
         marginTop: '6px',
         marginRight: '8px', 
         color: '#585959'
     }, 
     mainIcon: { 
         marginTop: '8px',
         color: '#63c7e6',
     },
     creds: { 
         position: 'absolute',
         bottom: '5vh',
         width: '100%',
         marginTop: '8px',
         fontFamily: 'Open Sans, sans-serif'
     }, 
     title: { 
        fontFamily: 'Lobster, cursive', 
        marginBottom: '8px'
     },
     main: { 
         position: 'relative',
         height: '100vh',
         width: '100vw',
         margin: 0
     }, 
     content: { 
        width: '100%',
        position: 'absolute',
        left: '50%',
        top: '50%',
        transform: 'translate(-50%, -50%)'
     }
  }));

const Main = () => {  
    const classes = useStyles();
    return (
            <div id="contact-section" className={classes.main}>
                <div className={classes.content}> 
                    <Avatar src={avatar} className={classes.avatar} />
                    <Typography className={classes.title} variant="h2">Andie Montoya</Typography> 
                    <Typography variant="h6" className={classes.wrapIcon}>
                        <MailOutline className={classes.sideIcon} /> andiemontoyeah@gmail.com
                    </Typography>  <br/>
                    <Typography variant="h6" className={classes.wrapIcon}>  
                        <Phone className={classes.sideIcon} /> 778-772-0785
                    </Typography>  
                    <br/>
                    <IconButton href="http://www.linkedin.com/in/andie-montoya-963a99170" className={classes.mainIcon}> 
                        <LinkedIn />
                    </IconButton>
                    <IconButton href="http://github.com/andiemontoyeah" className={classes.mainIcon}> 
                        <GitHub />
                    </IconButton>
                </div>
                <br/>
                <div className={classes.creds}> 
                    <p> Built with <a href="https://github.com/facebook/create-react-app">create-react-app</a> and <a href="https://material-ui.com/">material-ui</a>.</p>
                </div>
            </div> 
    ); 
}

export default Main;